<template>
  <v-container fluid fill-height>
	<v-layout align-center justify-center>
	  <v-flex md8>
	    <v-card class="elevation-10">
	      <v-toolbar color="success" dark flat>
	        <v-toolbar-title>
	        	SILAHKAN MASUK
	        </v-toolbar-title>
	      </v-toolbar>
	      <v-card-text>
	        <v-form ref="formDaftar">
	          <v-text-field
	            name="email"
	            label="Email"
	            id="email"
	            prepend-icon="email"
	            clearable
	            hint="example@mail.com"
	            v-model="email"
	            :rules="[rule.req, rule.email]"
	          ></v-text-field>
	          <v-text-field
	            name="pass"
	            label="Password"
	            id="pass"
	            prepend-icon="lock"
	            :append-icon="show ? 'visibility' : 'visibility_off'"
	            :type="show ? 'password' : 'text'"
	            @click:append="show = !show"
	            v-model="pass"
	            :rules="[rule.req, rule.minVal]"
	            @keyup.enter="submitDaftar"
	          ></v-text-field>
	        </v-form>
	        <!-- <v-layout row wrap class="mt-5">
	          <v-flex>
	          </v-flex>
	        </v-layout> -->
	      </v-card-text>
	      <v-card-actions>
	      	<v-btn class="caption pl-3 grey--text" route to="/signin" flat>Belum punya akun .?</v-btn>
	        <v-spacer></v-spacer>
            <v-btn color="error" @click="googleLogin" :loading="loadingSignin">masuk dengan google</v-btn>
	        <v-btn color="primary" @click="submitDaftar" :loading="loadingSignin">masuk</v-btn>
	      </v-card-actions>
	    </v-card>
	  </v-flex>
	</v-layout>
  </v-container>
</template>


<script>
	import Swal from 'sweetalert2'
	
	export default{
		data(){
			return{
				show:true,
				email:'',
				pass:'',
				rule:{
					req: v => !!v || "Harus di isi",
					email: value => {
			        	const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
			            return pattern.test(value) || 'Email apaan nih !'
			        },
					minVal: v => v.length >= 8 || "Harus lebih dari 8 karakter"
				},
				loadingSignin: false
			}
		},
		props:{
			
		},
		methods:{
			submitDaftar(){
				if (this.$refs.formDaftar.validate()) {
					this.loadingSignin = true
					firebase.auth().signInWithEmailAndPassword(this.email, this.pass).then(()=>{
						Swal.fire({
							title:'Berhasil !',
							text:'Berhasil masuk pak !',
							type:'success'
						}).then(()=>{

							this.$router.push('/')
						})
						
						this.loadingSignin = false

					}).catch((e)=>{
						Swal.fire({
							title:'Gagal !',
							text:'Email dan password yang anda masukkan salah !',
							type:'error'
						});
						this.loadingSignin = false

					})
				}
			},
			googleLogin(){
				this.loadingSignin = true
				let google = new firebase.auth.GoogleAuthProvider();

				firebase.auth().signInWithPopup(google).then((res)=>{
					var token = res.credential.accessToken
					var user = res.user
					this.loadingSignin = false

					Swal.fire({
						title:'Selamat datang !',
						text:'Anda berhasil masuk menggunakan akun google anda !',
						type:'success'
					}).then(()=>{
						this.$router.push({
							path:'/'
						})
					})


				}).catch((err)=>{
					this.loadingSignin = false
					alert('Gagal')
					console.log(err);
				})
			}
		}
	}
</script>
