<template>
	<v-layout column wrap align-end> 
  	  	<v-dialog scrollable transition="dialog-transition" max-width="800" v-model="showModelAddAnggota">
			<v-btn color="primary" slot="activator" dark >
				<v-icon>add</v-icon>
			</v-btn>
			<v-card>
			  <v-card-title primary-title class="font-weight-bold">
			    FORM TAMBAH ANGGOTA
			  </v-card-title>
			  <v-card-text>
			    <v-form ref="formAddData">
			      <v-layout row wrap>
			        <v-flex md6 sm12 xs12>
			          <v-text-field v-model="nama" :rules="rules" label="Masukkan nama" prepend-icon="assignment_ind" hint="Wajib diisi"></v-text-field>
			        </v-flex>
			        <v-flex md6 sm12 xs12>
			          <v-text-field v-model="telp" :rules="rules" label="Masukkan telpon" mask="###-###-###-###-###" counter="15" prepend-icon="phone"></v-text-field>
			        </v-flex>
			        <!-- <v-flex md12 sm12>
			          <v-select v-model="kab" :rules="rules" :items="itemKab" label="Pilih kabupaten" prepend-icon="location_city"></v-select>
			        </v-flex>
			        <v-flex md6 sm6>
			          <v-select v-model="kec" :rules="rules" :items="itemKec" label="Pilih kecamatan" prepend-icon="location_city"></v-select>
			        </v-flex>
			        <v-flex md6 sm6>
			          <v-select v-model="des" :rules="rules" :items="itemDes" label="Pilih desa" prepend-icon="location_city"></v-select>
			        </v-flex> -->
			      </v-layout>
			    </v-form>
			  </v-card-text>
			  <v-divider></v-divider>
			  <v-card-actions>
			    <v-spacer></v-spacer>
			    <v-btn color="blue" dark @click="submit" :loading="loadingButton">TAMBAH</v-btn>
			  </v-card-actions>
			</v-card>
  	  	</v-dialog> 
  	  <v-snackbar v-model="snackbarAddAnggota" multi-line vertical>
  	    Berhasil menambahkan anggota pak !
  	    <v-btn flat color="primary" @click.native="snackbarAddAnggota = false">Close</v-btn>
  	  </v-snackbar>
  	</v-layout>
</template>


<script>
import db from '@/fb'
import firebase from 'firebase'
	
	export default{
		data(){
			return{
				itemProv:[],
				itemKab:['Tuban'],
				itemKec:['Semanding'],
				itemDes:[
					'Tegalagung',
				    'Bektiharjo',
				    'Bejagung',
				    'Gedungombo',
				    'Boto',
				    'Gesing',
				    'Genaharjo',
				    'Sambongrejo',
				    'Ngino',
				    'Penambangan',
				    'Semanding',
				    'Prunggahan Wetan',
				    'Prunggahan Kulon',
				    'Jadi',
				    'Karang',
				    'Kowang',
				    'Tunah'
				],
				nama:'',
				telp:'',
				kab:'',
				kec:'',
				des:'',
				prov:'',
				loadingButton:false,
				showModelAddAnggota:false,
				snackbarAddAnggota:false,
				rules:[
					v => !!v || 'Harus di isi'
				],
				uid:firebase.auth().currentUser.uid,
				color:[
					"red darken-2",
					"pink darken-2",
					"purple darken-2",
					"deep-purple darken-2",
					"indigo darken-2",
					"blue darken-2",
					"light-blue darken-2",
					"cyan darken-2",
					"teal darken-2",
					"deep-orange darken-2",
					"brown darken-2",
					"blue-grey darken-2",
					"grey darken-2"
				]
			}
		},
		methods:{
			submit(){
				if (this.$refs.formAddData.validate()) {
					this.loadingButton = true
					const addItem = {
						nama: this.nama,
						telp: this.telp,
						// kab: this.kab,
						// kec: this.kec,
						// des: this.des,
						idUser: firebase.auth().currentUser.uid,
						color: this.color[Math.floor(Math.random() * this.color.length)]
					}

					firebase.database().ref(this.uid + '/kontak').push(addItem).then(()=>{
						this.loadingButton = false
						this.showModelAddAnggota = false
						this.snackbarAddAnggota = true

						this.nama = ''
						this.telp = ''
						// this.kab = ''
						// this.kec = ''
						// this.des = ''
						this.$refs.formAddData.resetValidation()
					})
				}
				// console.log(this.telp);
			}
		},
		created(){
			// GET PROFINSI INDONESIA
			// this.$http.get('http://dev.farizdotid.com/api/daerahindonesia/provinsi')
			// axios.get('https://dev.farizdotid.com/api/daerahindonesia/provinsi')
			// $.ajax({
			// 	crossOrigin: true,
			// 	url:'http://dev.farizdotid.com/api/daerahindonesia/provinsi',
			// 	method:'GET',
			// 	success:function (f) {
			// 		this.itemProv = f.semuaprovinsi
			// 		console.log(this.itemProv);

			// 	}
			// })
		}
	}
</script>