<template>
	<v-layout align-center wrap column>
		    <v-flex class="pt-4">
		      	<v-avatar size="120">
		      	  <img src="a.jpg" alt="alt">
		      	</v-avatar>

		    </v-flex>
		    <v-flex class="mt-3 text-uppercase">
		      <p class="body-2">{{name}}</p>
		    </v-flex>
		  </v-layout>
</template>

<script>
	export default{
		data(){
			return{
				name:''
			}
		},
		created(){
			if (firebase.auth().currentUser.displayName) {
				this.name = firebase.auth().currentUser.displayName
			}else{
				this.name = firebase.auth().currentUser.email
			}
		}
	}
</script>