<template>
	
</template>

<script>
	export default{
		data(){
			return{
				name:''
			}
		},
		created(){
			if (firebase.auth().currentUser.displayName) {
				this.name = firebase.auth().currentUser.displayName
			}else{
				this.name = firebase.auth().currentUser.email
			}
		}
	}
</script>