<template>
	<v-layout row wrap>
		<v-flex>
	  	    <v-card class="elevation-10">
	  	    	<v-card-title>
	  	    		<span class="title">Daftar kontak anda</span>
	  	    		<v-spacer></v-spacer>
	  	    		<!-- <v-tooltip bottom>
	  	    		  <span>Urutkan berdasarkan nama</span>
	  	    		  <v-btn color="success" slot="activator" small @click="sortData('nama')">text</v-btn>
	  	    		</v-tooltip> -->
	  	    	</v-card-title>
	  	    	<v-divider></v-divider>
		  	      <v-card-text>
		  	      	<v-flex v-if="itemAnggota == null">
						<v-card>
						  <v-card-text>
					  		<span class="title red--text">Kosong, tidak ada data pak !</span>
						  </v-card-text>
						</v-card>
					</v-flex>
					<v-flex v-if="itemAnggota == 'l'">
						<v-card>
						  <v-card-text>
							<v-layout column align-center justify-center>
							  <v-flex>
							    <v-progress-circular indeterminate color="blue" ></v-progress-circular>
							  </v-flex>
							</v-layout>
						  </v-card-text>
						</v-card>
					</v-flex>
					<v-flex xl12 lg12 md12 sm12 xs12 v-for="(item, i) in itemAnggota" :key="i" v-else>
						<v-list two-line subheader>
				          <v-list-tile avatar @click="messageTo(item.telp, item.nama)" ripple>
				            <v-list-tile-avatar>
				            	<v-avatar :color="item.color" size="42">
							      <span class="white--text headline text-capitalize">{{ item.nama.charAt(0) }}</span>
							    </v-avatar>
				            </v-list-tile-avatar>

				            <v-list-tile-content>
				              <v-list-tile-title class="font-weight-bold">{{item.nama}}</v-list-tile-title>
				              <v-list-tile-sub-title>{{item.telp}}</v-list-tile-sub-title>
				            </v-list-tile-content>

				            <v-list-tile-action>
				            	<v-menu offset-y>
				            	  <v-btn icon ripple slot="activator">
					                <v-icon color="grey lighten-1">more_vert</v-icon>
					              </v-btn>
				            	  <v-list>
				            	    <v-list-tile @click="prepareEdit(i)">
				            	    	<v-list-tile-action>
				            	    	  <v-icon>create</v-icon>
				            	    	</v-list-tile-action>
				            	      <v-list-tile-title>Edit</v-list-tile-title>
				            	    </v-list-tile>
				            	    <v-list-tile @click="deleteKontak(i, item.nama)" ripple>
				            	    	<v-list-tile-action>
				            	    	  <v-icon>delete</v-icon>
				            	    	</v-list-tile-action>
				            	      <v-list-tile-title>Hapus</v-list-tile-title>
				            	    </v-list-tile>
				            	  </v-list>
				            	</v-menu>
				            </v-list-tile-action>
				          </v-list-tile>
				        </v-list>
					</v-flex>

						<!-- <v-layout row wrap>
							  <v-flex md3 sm6 xs12>
							  	<div class="pa-2">
								    <div class="caption font-weight-bold">Nama Anggota</div>
								    {{ item.nama }}
							  	</div>
							  </v-flex>
							  <v-flex md2 sm6 xs12>
							  	<div class="pa-2">
								    <div class="caption font-weight-bold">Nomer Telepon</div>
								    {{ item.telp }}
							  	</div>
							  </v-flex>
							  <v-spacer></v-spacer>
							  <v-flex md2 sm4 xs12>
							  	<div class="pa-2">
								    <div class="caption font-weight-bold">Desa</div>
								    {{ item.des }}
							  	</div>
							  </v-flex>
							  <v-flex md2 sm4 xs12>
							  	<div class="pa-2">
								    <div class="caption font-weight-bold">Kecamatan</div>
								    {{ item.kec }}
							  	</div>
							  </v-flex>
							  <v-flex md1 sm4 xs12>
							  	<div class="pa-2">
								    <div class="caption font-weight-bold">Kabupaten</div>
								    {{ item.kab }}
							  	</div class="pa-2">
							  </v-flex>
							  <v-flex md2 sm12 xs12 class="pa-2">
							  	<v-layout row align-center wrap>
							  		<v-flex md12 sm12 xs12>
									    <v-layout column align-center justify-center>
									      <v-btn color="success" block small>text</v-btn>
									    </v-layout>
									  </v-flex>
							  		<v-flex>
									  	<v-tooltip top>
											<v-btn slot="activator" small dark block color="warning" @click="prepareEdit(i)"><v-icon>create</v-icon></v-btn>
									  		<span>Edit</span>  
									  	</v-tooltip>
							  		</v-flex>
							  		<v-flex>
									  	<v-tooltip bottom>
											<v-btn slot="activator" small dark block color="red" @click="deleteAnggota(i, item.nama)"><v-icon>delete</v-icon></v-btn>
									  	 	<span>Hapus</span>
									  	</v-tooltip>
							  		</v-flex>
							  	</v-layout>
							  </v-flex>
							</v-layout>
						<v-divider class="my-3"></v-divider> -->

					<!-- SNACK BAR -->
					<v-snackbar v-model="snackbarDelete" multi-line vertical color="success">
					  Berhasil menghapus pak !
					  <v-btn flat color="primary" @click.native="snackbarDelete = false">Close</v-btn>
					</v-snackbar>
					<v-snackbar v-model="snackbarEdit" multi-line vertical>
					  Berhasil mengedit pak !
					  <v-btn flat @click.native="snackbarEdit = false">Close</v-btn>
					</v-snackbar>

					<!-- /VDIALOG\ -->
					<v-dialog scrollable transition="dialog-transition" max-width="800" v-model="showModalEdit">
						<v-card>
						  <v-card-title primary-title class="font-weight-bold">
						    FORM EDIT ANGGOTA
						  </v-card-title>
						  <v-card-text>
						    <v-form ref="formEditData">
						      <v-layout row wrap>
						        <v-flex md6 sm12 xs12>
						          <v-text-field v-model="edit.nama" :rules="rules" label="Masukkan nama" prepend-icon="assignment_ind" hint="Wajib diisi"></v-text-field>
						        </v-flex>
						        <v-flex md6 sm12 xs12>
						          <v-text-field v-model="edit.telp" :rules="rules" label="Masukkan telpon" mask="###-###-###-###-###" counter="15" prepend-icon="phone"></v-text-field>
						        </v-flex>
						        
						        <!-- <v-flex md12 sm12>
							          <v-select v-model="edit.kab" :rules="rules" :items="itemKab" label="Pilih kabupaten" prepend-icon="location_city"></v-select>
							        </v-flex>
							        <v-flex md6 sm6>
							          <v-select v-model="edit.kec" :rules="rules" :items="itemKec" label="Pilih kecamatan" prepend-icon="location_city"></v-select>
							        </v-flex>
							        <v-flex md6 sm6>
							          <v-select v-model="edit.des" :rules="rules" :items="itemDes" label="Pilih desa" prepend-icon="location_city"></v-select>
						        </v-flex> -->
						      </v-layout>
						    </v-form>
						  </v-card-text>
						  <v-divider></v-divider>
						  <v-card-actions>
						    <v-spacer></v-spacer>
						    <v-btn color="error" dark @click="showModalEdit = false" :loading="loadingButtonEdit">BATAL</v-btn>
						    <v-btn color="blue" dark @click="submitEdit(idEdit)" :loading="loadingButtonEdit">EDIT</v-btn>
						  </v-card-actions>
						</v-card>
			  	  	</v-dialog> 
		  	      </v-card-text>
	  	    </v-card>
	  	  </v-flex>

	  	  <v-dialog
	  	    v-model="modalMessage"
	  	    max-width="500"
	  	    transition="dialog-transition"
	  	  >
	  	  	
	  	  	<v-card>
	  	  		<v-card-title primary-title>
	  	  		  Kirim pesan ke {{kirimPesanKe}}
	  	  		</v-card-title>
	  	  	  <v-card-text>
	  	  	    Ini card
	  	  	  </v-card-text>
	  	  	</v-card>
	  	  </v-dialog>
	</v-layout>
</template>


<script>
	
	import db from '@/fb'
	import Swal from 'sweetalert2'

	export default{
		data(){
			return{
				itemAnggota:'l',
				snackbarDelete:false,
				snackbarEdit:false,
				showModalEdit: false,
				edit:{
					nama:'',
					telp:'',
					kab:'',
					kec:'',
					des:'',
					prov:'',
				},
				loadingButtonEdit:false,
				rules:[
					v => !!v || 'Harus di isi'
				],
				itemKab:['Tuban'],
				itemKec:['Semanding'],
				itemDes:[
					'Tegalagung',
				    'Bektiharjo',
				    'Bejagung',
				    'Gedungombo',
				    'Boto',
				    'Gesing',
				    'Genaharjo',
				    'Sambongrejo',
				    'Ngino',
				    'Penambangan',
				    'Semanding',
				    'Prunggahan Wetan',
				    'Prunggahan Kulon',
				    'Jadi',
				    'Karang',
				    'Kowang',
				    'Tunah'
				],
				itemProv:[],
				statusKeberhasilan:'',
				uid:firebase.auth().currentUser.uid,
				modalMessage:false,
				kirimPesanKe:''
			}
		},
		methods:{
			deleteKontak(id, nama){
				Swal.fire({
					title:'Peringatan !',
					text:'Apakah anda ingin menghapus ' + nama,
					showCancelButton:true,
					type:'warning'
				}).then((res)=>{
					if (res.value) {
						firebase.database().ref(this.uid + '/kontak/' + id).remove().then(()=>{
							// Swal.fire({
							// 	title:'Berhasil !',
							// 	text:'Berhasil menghapus anggota',
							// 	type:'success'
							// })
							// this.statusKeberhasilan = 'menghapus'
							this.showModalEdit = false
							this.snackbarDelete = true
						})
					}
				})
			},
			prepareEdit(id){
				firebase.database().ref(this.uid + '/kontak/' + id).on('value', (get)=>{
					const data = get.val();
					this.edit.nama = data.nama
					this.edit.telp = data.telp
					// this.edit.des = data.des
					// this.edit.kec = data.kec
					// this.edit.kab = data.kab
					this.idEdit = id
					this.showModalEdit = true
				})
			},
			submitEdit(id){
				if (this.$refs.formEditData.validate()) {
					this.loadingButtonEdit = true
					firebase.database().ref(this.uid + '/kontak/' + id).update({
						nama:this.edit.nama,
						telp:this.edit.telp,

					}).then(()=>{
						this.showModalEdit = false
						this.loadingButtonEdit = false

						this.edit.nama = ''
						this.edit.telp = ''
						this.edit.kab = ''
						this.edit.kec = ''
						this.edit.des = ''
						this.$refs.formEditData.resetValidation()

						// this.statusKeberhasilan = 'mengedit'
						this.snackbarEdit = true
					})
				}
			},
			sortData(by){
				this.itemAnggota.sort((a,b) => a[by] < b[by] ? -1 : 1 )
			},
			messageTo(to, nama){
				this.modalMessage = true
				this.kirimPesanKe = nama
				console.log(to);
			}
		},
		computed:{

		},
		created(){
			firebase.database().ref(this.uid + '/kontak').on('value', (snapshot)=>{
				this.itemAnggota = snapshot.val()
				// console.log(snapshot.val());
			})
			// console.log(this.itemAnggota);
			// $.ajax({
			// 	crossOrigin: true,
			// 	url:'http://dev.farizdotid.com/api/daerahindonesia/provinsi',
			// 	method:'GET',
			// 	success:function (f) {
			// 		var dataProv = JSON.parse(f)
			// 		console.log(dataProv.semuaprovinsi);
			// 		this.itemProv = dataProv.semuaprovinsi

			// 	}
			// })
		}
	}
</script>