<template>
  <v-container fluid fill-height>
	<v-layout align-center justify-center>
	  <v-flex md8>
	    <v-card class="elevation-10">
	      <v-toolbar color="primary" dark flat>
	        <v-toolbar-title>
	        	SILAHKAN MENDAFTAR
	        </v-toolbar-title>
	      </v-toolbar>
	      <v-card-text>
	        <v-form ref="formDaftar">
	          <v-text-field
	            name="email"
	            label="Email"
	            id="email"
	            prepend-icon="email"
	            clearable
	            hint="example@mail.com"
	            v-model="email"
	            :rules="[rule.req, rule.email]"
	          ></v-text-field>
	          <v-text-field
	            name="pass"
	            label="Password"
	            id="pass"
	            prepend-icon="lock"
	            :append-icon="show ? 'visibility' : 'visibility_off'"
	            :type="show ? 'password' : 'text'"
	            @click:append="show = !show"
	            v-model="pass"
	            :rules="[rule.req, rule.minVal]"
	          ></v-text-field>
	        </v-form>
	      </v-card-text>
	      <v-card-actions>
	      	<v-btn class="caption pl-3 grey--text" route to="/login" flat>Sudah punya akun .?</v-btn>
	        <v-spacer></v-spacer>
	        <v-btn color="primary" @click="submitDaftar" :loading="loadingSignin">daftar</v-btn>
	      </v-card-actions>
	    </v-card>
	  </v-flex>
	</v-layout>
  </v-container>
</template>


<script>
	import Swal from 'sweetalert2'
	
	export default{
		data(){
			return{
				show:true,
				email:'',
				pass:'',
				rule:{
					req: v => !!v || "Harus di isi",
					email: value => {
			        	const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
			            return pattern.test(value) || 'Email apaan nih !'
			        },
					minVal: v => v.length >= 8 || "Harus lebih dari 8 karakter"
				},
				loadingSignin: false
			}
		},
		props:{
			
		},
		methods:{
			submitDaftar(){
				if (this.$refs.formDaftar.validate()) {
					this.loadingSignin = true
					firebase.auth().createUserWithEmailAndPassword(this.email, this.pass).then(()=>{
						Swal.fire({
							title:'Berhasil !',
							text:'Berhasil mendaftar pak !',
							type:'success'
						}).then(()=>{

							this.$router.push('/')
						})
						
						this.loadingSignin = false

					}).catch((e)=>{
						Swal.fire({
							title:'Gagal !',
							text:'Email yang anda masukkan sudah ada pak ! silahkan langsung login saja',
							type:'error'
						});
						this.loadingSignin = false

					})
				}
			}
		}
	}
</script>
