<template>
	<nav>
		<v-toolbar color="blue accent-4" class="white--text" dark fixed >
		  <v-toolbar-side-icon class="" @click="drawerSwitch()"></v-toolbar-side-icon>
		  <v-toolbar-title>
		  	<span class="text-width-bold">SELAMAT DATANG</span>
		  </v-toolbar-title>
		  <v-spacer></v-spacer>
		  <v-toolbar-items class="hidden-sm-and-down">
		  	<!-- <v-btn flat v-for="(menu, i) in itemMenu">{{ menu.nama }}</v-btn> -->
		  	<v-btn flat>
				{{name}}
		  	</v-btn>
		  	<v-btn flat @click="keluar">
				<v-icon left>exit_to_app</v-icon>
				Logout
		  	</v-btn>
		  </v-toolbar-items>
		</v-toolbar>

		<v-navigation-drawer v-model="drawer" app  temporary :mini-variant="mini">
			<v-list v-if="mini" >
				<v-list-tile @click="mini = !mini">
				  <v-list-tile-action>
				    <v-icon>chevron_right</v-icon>
				  </v-list-tile-action>
				</v-list-tile>
			</v-list>
			<!--  -->
			<v-list>
			  <v-list-tile avatar>
			    <v-list-tile-avatar>
			      <img :src="urlPhoto">
			    </v-list-tile-avatar>
			    <v-list-tile-content>
			      <v-list-tile-title>{{name}}</v-list-tile-title>
			    </v-list-tile-content>
			    <v-list-tile-action>
		            <v-btn icon @click.stop="mini = !mini">
		              <v-icon>chevron_left</v-icon>
		            </v-btn>
		          </v-list-tile-action>
			  </v-list-tile>
			</v-list>
		  <!--  -->
		  	<!-- <layoutProfileModeSatu></layoutProfileModeSatu> -->
		  	<!-- <layoutProfileModeDua></layoutProfileModeDua> -->
		  <!--  -->
		  <v-divider></v-divider>
		  <v-list v-for="(menu, i) in itemMenu">
		    <v-list-tile route :to="menu.link" :key="menu.i">
		    	<v-list-tile-action>
		    	  <v-icon>{{menu.icon}}</v-icon>
		    	</v-list-tile-action>
		    	<v-list-tile-content>
		    		<v-list-tile-title>{{menu.nama}}</v-list-tile-title>
		    	</v-list-tile-content>
		    </v-list-tile>
		  </v-list>
			  <v-divider class="hidden-md-and-up"></v-divider>
			  	<v-list class="hidden-md-and-up">
			  	  	<v-list-tile @click="keluar">
				    	<v-list-tile-action>
				    	  <v-icon>exit_to_app</v-icon>
				    	</v-list-tile-action>
				    	<v-list-tile-content>
				    		<v-list-tile-title>Keluar</v-list-tile-title>
				    	</v-list-tile-content>
			    	</v-list-tile>
			  	</v-list>
		</v-navigation-drawer>
	</nav>
</template>


<script>
	import firebase from 'firebase'
	import Swal from 'sweetalert2'

	import layoutProfileModeSatu from '@/components/layoutProfileModeSatu'
	import layoutProfileModeDua from '@/components/layoutProfileModeDua'

	export default {
		data(){
			return{
				itemMenu:[
					{
						nama:'Beranda',
						icon:'dashboard',
						link:'/'
					},
					{
						nama:'File',
						icon:'folder',
						link:'/file'
					}
				],
				drawer:false,
				name:'',
				mini:false
			}
		},
		methods:{
			drawerSwitch(){
				if (this.drawer == false) {
					this.drawer = true
				}else {
					
					this.drawer = false
				}
			},
			keluar(){
				Swal.fire({
					title:'Peringatan !',
					text:'Apakah anda ingin keluar .?',
					type:'warning',
					showCancelButton:true					
				}).then((res)=>{
					if (res.value) {
						firebase.auth().signOut().then(()=>{
							Swal.fire({
								title:'Berhasil !',
								text:'Berhasil keluar pak !',
								type:'success'
							}).then(()=>{
								this.$router.push({
									path:'/login'
								})
							})
						})
					}
				})
			}
		},
		created(){
			if (firebase.auth().currentUser.displayName) {
				this.name = firebase.auth().currentUser.displayName
			}else{
				this.name = firebase.auth().currentUser.email
			}
		},
		components:{
			layoutProfileModeSatu,
			layoutProfileModeDua,

		},
		computed:{
			urlPhoto(){
				if (firebase.auth().currentUser.photoURL) {
					return firebase.auth().currentUser.photoURL
				}else{
					return "a.jpg"
				}
			}
		}

	}
</script>